@import url(https://fonts.googleapis.com/css2?family=Signika+Negative:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.preloader-container {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.preloader-loader {
  overflow: hidden;
  border-bottom: 5px solid var(--bg-colour);
  box-shadow: 0 4px 5px -5px black;
  -webkit-animation: show 1s ease-in-out;
          animation: show 1s ease-in-out;
}

.preloader-img {
  height: 130px;
}

@-webkit-keyframes show {
  0% {
    height: 0px;
  }
  100% {
    height: 135px;
  }
}

@keyframes show {
  0% {
    height: 0px;
  }
  100% {
    height: 135px;
  }
}

.navbar-container {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  background-color: var(--bg-colour);
  box-shadow: 0 4px 5px -5px black;
}

.nav-brand {
  width: 50px;
  margin-left: calc(var(--page-margins) - 10px);
}

.nav-links {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-right: calc(var(--page-margins) - 10px);
}

.nav-item {
  margin-left: 40px;
}

.nav-text {
  text-decoration: none;
  font-size: 22px;
  font-weight: 600;
  color: var(--text-colour);
}
.nav-text:hover {
  cursor: pointer;
  color: var(--accent-colour);
}

nav .navbar-toggler {
  margin-right: calc(var(--page-margins) - 10px);
}

nav .navbar-collapse {
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .nav-item {
    margin-left: 0px;
  }
}

.banner-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.banner-container h1 {
  margin: 0 !important;
  padding: 0 !important;
}

.banner-pretitle {
  font-size: calc(14px + 1vmin);
  color: var(--accent-colour);
}
.banner-title {
  font-size: calc(50px + 6vmin);
}
.banner-subtitle {
  font-size: calc(14px + 2vmin);
  color: var(--secondary-text-colour);
}

:root {
  --bg-colour: #03002E;
  --secondary-bg-colour: #03254C;
  --text-colour: #f2fbff;
  --secondary-text-colour: #365880;
  --accent-colour: #3dd3f4;

  --page-margins: 400px;

  --ftprojects-br: 10px;
  --ftprojects-side-margin: 50px;
}

.web-container {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  color: #f2fbff;
  color: var(--text-colour);
  background-color: #03002E;
  background-color: var(--bg-colour);
  font-family: "Signika Negative", sans-serif;
  /* font-family: "Comfortaa", cursive; */
}

.page-container {
  width: calc(100vw - 2 * 400px);
  width: calc(100vw - 2 * var(--page-margins));
  height: 100%;
}

.homepage-title {
  font-size: 44px;
  margin-bottom: 40px;
}
.coloured {
  color: #3dd3f4;
  color: var(--accent-colour);
}

/* -------------------- PRELOADER -------------------- */
.page-shown {
  opacity: 1;
  /* animation: fadeIn 500ms; */
  transition: all 500ms ease-in-out;
}
.page-hidden {
  opacity: 0;
  /* animation: fadeOut 500ms; */
  transition: all 500ms ease-in-out;
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

Navbar {
  padding: 0 !important;
  margin: 0 !important;
}

@media only screen and (max-width: 1420px) {
  :root {
    --page-margins: 150px;
  }
}
@media only screen and (max-width: 992px) {
  :root {
    --page-margins: 100px;
  }
}
@media only screen and (max-width: 768px) {
  :root {
    --page-margins: 50px;
  }
}

/* -------------------- SCOLLBAR -------------------- */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #03002E;
  background: var(--bg-colour);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #03254C;
  background: var(--secondary-bg-colour);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #365880;
  background: var(--secondary-text-colour);
}

