.ftprojects-item {
  width: 100%;
  height: 40vh;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.ftprojects-item:hover {
  cursor: pointer;
}
.ftprojects-item:hover .ftprojects-img {
  /* box-shadow: 0px 0px 10px white; */
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: 0.3s all;
}
.ftprojects-item:hover .ftprojects-link {
  opacity: 1;
  transition: 0.3s all;
}

.ftprojects-text-container {
  width: 45%;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.ftprojects-text-right {
  align-items: flex-end;
  text-align: right;
  padding-right: var(--ftprojects-side-margin);
}
.ftprojects-text-left {
  align-items: flex-start;
  text-align: left;
  padding-left: var(--ftprojects-side-margin);
}
.ftprojects-name {
  color: var(--accent-colour);
  font-weight: 600;
  font-size: calc(16px + 2vmin);
}
.ftprojects-description {
  width: 110%;
  padding: 15px;
  z-index: 5;
  background-color: var(--secondary-bg-colour);
  border-radius: 5px;
  font-size: calc(10px + 1vmin);
  margin-top: 10px;
  margin-bottom: 10px;
}

.ftprojects-languages {
  width: 100%;
  display: flex;
  align-items: center;
}
.ftprojects-languages-left {
  justify-content: flex-end;
}
.ftprojects-languages-right {
  justify-content: flex-start;
}
.ftprojects-language-h1 {
  color: var(--secondary-text-colour);
  font-size: calc(6px + 1vmin);
  font-weight: 600;
  margin: 0;
}
.ftprojects-language-h1-right {
  margin-right: 8px;
}
.ftprojects-language-h1-left {
  margin-left: 8px;
}

.ftprojects-img-container {
  width: 55%;
  display: block;
  overflow: hidden;
  border-radius: 5px;
}

.ftprojects-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.3s all;
}

.ftprojects-link {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  bottom: 25px;
  z-index: 10;
  opacity: 0;
  transition: 0.3s all;
}
.ftprojects-arrow {
  margin-left: 5px;
}

.footer-container {
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-text {
  font-size: calc(14px + 1vmin);
  font-weight: 600;
  color: var(--secondary-text-colour);
}

.footer-img {
  width: 50px;
}

.contact-container {
  position: fixed;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  color: var(--secondary-bg-colour);
}
.contact-left {
  left: 3%;
}
.contact-right {
  right: -1%;
}

.contact-icon {
  font-size: calc(14px + 1vmin);
  margin-bottom: 25px;
  transition: 0.2s all;
}
.contact-email {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  margin-bottom: 100px;
  font-size: calc(10px + 1vmin);
  transition: 0.2s all;
}
.contact-icon:hover,
.contact-email:hover {
  cursor: pointer;
  color: var(--accent-colour);
  transition: 0.2s all;
}

.contact-vl {
  width: 2px;
  height: 120px;
  background-color: var(--secondary-bg-colour);
}

.about-container {
  display: flex;
}

.about-text {
  margin-right: 20px;
}
.about-text p {
  font-size: 20px;
}

.about-img {
  width: 20vw;
}

.projects-marquee {
  margin-bottom: 100px;
}
.projects-marquee-item {
  width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
  border-radius: 10px;
  /* background-color: red; */
}
.projects-marquee-item:hover {
  cursor: pointer;
  background-color: var(--secondary-bg-colour);
}
.projects-marquee-img {
  height: 150px;
  margin-bottom: 5px;
}
.projects-marquee-text {
  font-size: 24px;
}

.projects-item {
  margin-bottom: 100px;
}

.projects-item-banner {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}
.projects-item-banner-left {
  text-align: left;
}
.projects-item-banner-right {
  text-align: right;
}
.projects-item-img-container {
  height: 40vh;
}
.projects-item-img {
  height: 100%;
  max-width: 100%;
  object-fit: contain;
}

.projects-item-overview {
  margin: 20px;
}
.projects-item-overview-name {
  color: var(--accent-colour);
  font-size: calc(18px + 2vmin);
  font-weight: 600;
  margin-bottom: -5px;
}
.projects-item-overview-type {
  color: var(--secondary-text-colour);
  font-size: calc(12px + 1vmin);
  margin-bottom: 20px;
}
.projects-item-overview-timeframe {
  font-size: calc(12px + 1vmin);
  margin-bottom: 20px;
}
.projects-item-overview-languages-container {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}
.projects-item-overview-languages-container-right {
  justify-content: flex-end;
}
.projects-item-overview-languages-container-left {
  justify-content: flex-start;
}
.projects-item-overview-language {
  font-size: calc(10px + 1vmin);
  color: var(--secondary-text-colour);
}
.projects-item-overview-language-left {
  margin-right: 10px;
}
.projects-item-overview-language-right {
  margin-left: 10px;
}
.projects-item-links {
  font-size: calc(16px + 2vmin);
}
.projects-icon {
  transition: 0.2s all;
}
.projects-icon:hover {
  cursor: pointer;
  color: var(--accent-colour);
  transition: 0.2s all;
}
.projects-icon-left {
  margin-right: 20px;
}
.projects-icon-right {
  margin-left: 20px;
}

.projects-description {
  font-size: calc(10px + 1vmin);
}

